.accessories {
    padding: 10rem 0 15rem;
    background-color: $white;
    &__inner {
        display: flex;
        justify-content: space-between;
        padding-top: 7rem;
        @media (max-width: 676px) {
            flex-wrap: wrap;
        }
    }
    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 24%;
        padding: 3rem 0;
        &-img {
            width: 15rem;
            height: 15rem;
            img {
                display: block;
                width: 100%;
                height: auto;
            }
        }
        &-title {
            margin: 0;
            padding: 2rem 0;
            font-weight: 700;
            font-size: 2.2rem;
            line-height: 1;
            text-align: center;
            color: $grey-dark;
        }
        &-price {
            margin: 0;
            padding: 0.5rem 1rem;
            border-radius: 0.5rem;
            background-color: $red;
            font-weight: 500;
            font-size: 2rem;
            line-height: 1;
            text-align: center;
            color: $white-dark;
        }
        @media (max-width: 676px) {
            width: 45%;
        }
    }
}